import { Card ,Divider,Space,Typography} from 'antd';
import "./ProductCardStyle.css";
import React from 'react'
import { ISearchInput } from '..';
export interface IProductCard{
    image:string;
    type:string;
    price:number;
    location:string;
}
export const ProductCard = (props:ISearchInput) => {
    const{image,location,price,type}=props;
  const { Title,Text } = Typography;
  return (
    <Card
      hoverable
      style={{ width: 300 ,}}
      cover={
        <img
          alt='example'
          src={image} />
      }
    >
      <Space >
      <Title level={5}>{type}</Title>
      <Divider  type="vertical"  style={{backgroundColor:'black',width:2,top:-3}}></Divider>
      <Title level={5}>{price}</Title>
      </Space>
      <Text keyboard>{location}</Text>

    </Card>
  )
}
ProductCard.default={
    image:"https://i0.wp.com/civillane.com/wp-content/uploads/2022/03/2BHK-Interior-Design-Cost-In-Pune.jpg?fit=900%2C615&ssl=1",
    type:"2 BHK",
    price:'2500 / month',
    location:'Banglore'
}